/**
 * 
 * 
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: c
 * API: 
 * 
 */

let baseUrl = ''; 
let routerMode = 'hash';
let imgBaseUrl;


if (process.env.NODE_ENV == 'development') {
	// baseUrl = 'http://tweb.fundog.cn';
	baseUrl = '/apis'
  	imgBaseUrl = 'http://tweb.fundog.cn/img/';

}else if(process.env.NODE_ENV == 'production'){
	baseUrl = 'http://tweb.fundog.cn';
  	imgBaseUrl = 'http://tweb.fundog.cn/img/';
}

let API = {
	login: baseUrl + "/auth/login", //
	logout: baseUrl + "/auth/logout", //
	app_navtab: baseUrl + "/home/app_navtab", //tabBar
	home_data: baseUrl + "/home/home_data", //
	baseinfo: baseUrl + "/auth/baseinfo", //
	left_category_data: baseUrl + "/category/left_category_data", //tab
	right_category_data: baseUrl + "/category/right_category_data", //
	cart_index_v: baseUrl + "/cart/index_v", //
	cart_update: baseUrl + "/cart/update", //
	cart_check: baseUrl + "/cart/update_check", //
	wscart_index: baseUrl + "/wscart/index", //
	wscart_update: baseUrl + "/wscart/update", //
	wscart_check: baseUrl + "/wscart/update_check", //
	collect_add: baseUrl + "/collect/add", //
	cart_delete: baseUrl + "/cart/delete", //
	wscart_delete: baseUrl + "/wscart/delete", //
	bupload_single: baseUrl + "/bupload/single", //
	user_modifyinfo: baseUrl + "/user/modifyinfo", //
	order_index: baseUrl + "/order/index", //
	order_detail: baseUrl + "/order/detail", //
	order_delete: baseUrl + "/order/delete", //
	order_redo: baseUrl + "/order/redo", //
	collect_index: baseUrl + "/collect/index", //
	collect_cancel: baseUrl + "/collect/cancel", //
	address_index: baseUrl + "/address/index", //
	address_set_default: baseUrl + "/address/set_default", //
}
export {
	baseUrl,
	routerMode,
	imgBaseUrl,
	API,
}


